"use strict";

import './style.less';

const Input = require('modules/input/index');
const Validator = require('modules/validator/index');

// 
class Title extends React.Component {
	render(){
		let me = this;
		let {props} = me;
		let {originData} = props;
		return (
			<div className="form">
				<Validator executeSuccess={Actions(me).success}>
					{/*  */}
					<Input type="text" 
						onChange={Actions(me).change.bind(null, 'name')}
						value={originData.name}
						placeholder=""
						rule="required"
						errMessage=""
						name="name" /><br /><br />
					{/*  */}
					<Input type="number" 
						onChange={Actions(me).change.bind(null, 'number')}
						value={originData.number}
						placeholder=""
						rule="required"
						errMessage=""
						name="number" /><br /><br />
					{/*  */}
					<Input type="password" 
						onChange={Actions(me).change.bind(null, 'pwd')}
						value={originData.pwd}
						placeholder=""
						rule="required,min(3),max(8)"
						errMessage=",,8"
						name="pwd" /><br /><br />
					<Input type="password" 
						placeholder=""
						rule={function(val){ if(val === ''){return ''};if( val !== originData.pwd ){return '';} }}
						name="pwd1" /><br /><br />
					{/*  */}
					<Input type="select"
						onChange={Actions(me).change.bind(null, 'age')}
						value={originData.age}
						rule="required"
						errMessage=""
						name="age" >
						<option value=""></option>
						<option value="17">a17</option>
						<option value="18">a18</option>
						<option value="19">a19</option>
						<option value="20">a20</option>
					</Input><br /><br />
					{/*  */}
					{/*  */}
					<Input type="file" name="file" value=" " /><br /><br />
					{/*  */}
					<Input type="textarea"
						onChange={Actions(me).change.bind(null, 'content')}
						rule="required,max(11)"
						errMessage=",11"
						placeholder=""
						value={originData.content}
						name="content" /><br /><br />
					{/*  */}
					<Input type="submit" value=" " />
					{/*  */}
					<Input type="reset" className="fn-ML5" value=" " />
					{/*  */}
					<Input type="button" className="fn-ML5" value=" " />

				</Validator>
			</div>
		);
	}
};

module.exports = Title;




